"use client";

import React from 'react';
import { WikiLayout } from '@/components/layout/WikiLayout';
import { compileMDX } from 'next-mdx-remote/rsc';
import fs from 'fs';
import path from 'path';
import matter from 'gray-matter';

export default function HipaaPage() {
  const [content, setContent] = React.useState<React.ReactNode | null>(null);
  const [frontmatter, setFrontmatter] = React.useState<any>({});

  React.useEffect(() => {
    async function loadContent() {
      try {
        // This is a client component, so we need to use a fetch request to get the content
        const response = await fetch('/api/content?path=hipaa/index.md');
        if (!response.ok) {
          throw new Error('Failed to load content');
        }
        
        const data = await response.json();
        setContent(data.content);
        setFrontmatter(data.frontmatter);
      } catch (error) {
        console.error('Error loading content:', error);
      }
    }

    // For now, let's use a static fallback to ensure we have something to display
    const staticContent = `
# HIPAA Compliance Hub

## Overview
This HIPAA Compliance Hub serves as the central resource for ensuring our organization maintains strict adherence to HIPAA regulations in our EHR/CCM software development and operations. All team members are required to follow these guidelines without exception.

## Quick Navigation

### Core Requirements
- [Technical Security Standards](/wiki/hipaa/core/technical-security)
- [Access Control and Authentication](/wiki/hipaa/core/access-control)
- [Data Integrity and Backup](/wiki/hipaa/core/data-integrity)
- [Privacy and Consent Management](/wiki/hipaa/core/privacy)
- [Incident Response](/wiki/hipaa/core/incident-response)
- [CCM-Specific Requirements](/wiki/hipaa/core/ccm-specific-requirements)

### Implementation Tools
- [Compliance Dashboard](/wiki/hipaa/tools/compliance-dashboard)
- [Interactive Checklists](/wiki/hipaa/tools/checklists)
- [Role-Based Guides](/wiki/hipaa/tools/role-based-guides)
- [Documentation Templates](/wiki/hipaa/tools/documentation-templates)

## LLM Compliance
- [HIPAA Compliance for LLMs in Healthcare](/wiki/hipaa/llm-compliance)
`;

    setContent(
      <div className="prose prose-slate dark:prose-invert max-w-none">
        <div className="mb-8">
          <h1 className="text-3xl font-extrabold tracking-tight text-gray-900 dark:text-white mb-3">
            HIPAA Compliance Documentation
          </h1>
          
          <p className="text-xl text-gray-500 dark:text-gray-400">
            Comprehensive guide to HIPAA compliance for healthcare software development
          </p>
          
          <div className="text-sm text-gray-500 dark:text-gray-400 mt-2">
            Last updated: March 13, 2025
          </div>
          
          <div className="flex flex-wrap gap-2 mt-4">
            {['hipaa', 'compliance', 'healthcare'].map((tag) => (
              <span 
                key={tag}
                className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-100"
              >
                {tag}
              </span>
            ))}
          </div>
        </div>
        
        <div className="mdx-content" dangerouslySetInnerHTML={{ __html: staticContent }}></div>
      </div>
    );
    
    // We'll try to load the actual content, but fall back to static if it fails
    loadContent();
  }, []);

  return (
    <WikiLayout>
      <article className="prose prose-slate dark:prose-invert max-w-none">
        {content}
      </article>
    </WikiLayout>
  );
}
